// Alerts

.alert-variant(@background; @border; @text-color) {
  background-color: @background;
  border-color: @border;
  color: @text-color;

  hr {
    border-top-color: darken(@border, 5%);
  }
  .alert-link {
    color: darken(@text-color, 10%);
  }
}

.alerts-variant(@color) {
    border-left-color: @color;
    color: @color;
    .border {
        background-color: @color;

    }
}
